<template>
	<Page zeroPadding :loading="loading">
		<div class="studied-words">
			<div><b>Изучаемые слова</b>: {{ studiedWords.length }}</div>
			<Button design="link" @click="wordsModalOpen = true">{{
				studiedWords.length === 0 ? 'Добавить' : 'Редактировать'
			}}</Button>
		</div>

		<div class="placeholder" v-if="studiedWords.length === 0">
			Выберите слова, чтобы начать.
		</div>
		<div class="placeholder" v-else-if="wordsModalOpen">
			Закройте окно, чтобы начать.
		</div>
		<div class="training-page" v-else>
			<div class="card">
				<div class="translate-text">
					{{ trainingList[trainingIndex].sentence[1] }}
				</div>
				<div class="original-text">
					{{ cardAnswerPre }}
					<TextField
						:value="answer"
						@input="answer = $event"
						@keypress="$event.key === 'Enter' && checkNext()"
						class="original-text-input"
						:style="{ width: wordLength + 'px' }"
						ref="textField"
					/>
					{{ cardAnswerPost }}
				</div>
			</div>
			<div class="button-container">
				<ButtonProgress
					:progress="trainingIndex / trainingList.length"
					design="action"
					size="big"
					@click="checkNext"
				>
					{{trainingIndex === trainingList.length - 1 ? `Finish` : 'Next'}}
				</ButtonProgress>
			</div>
		</div>

		<Modal :open="wordsModalOpen" :width="600" key="wordsModal">
			<div class="modal-content">
				<div class="top">
					<p>Выберите <b>{{WORDS}}</b> слов для изучения (перевод которых вы уже знаете).</p>
					<TextField
						:value="searchWord"
						@input="
							searchWord = $event;
							wordsFoundSelected = [];
						"
						placeholder="Введите слово для поиска"
					/>
				</div>
				<div class="add-panel-items" v-if="wordsFound">
					<div
						v-for="item in wordsFound"
						:key="item[0]"
						:class="['item', studiedWords.includes(item[2]) && 'selected']"
						@click="onWordClick(item)"
					>
						<svg
							width="13"
							height="13"
							viewBox="0 0 13 13"
							fill="none"
							xmlns="http://www.w3.org/2000/svg"
						>
							<path
								d="M10.8192 5.14396C10.8192 4.98771 10.769 4.85936 10.6685 4.75891L9.90681 4.00556C9.80078 3.89954 9.67522 3.84652 9.53013 3.84652C9.38504 3.84652 9.25949 3.89954 9.15346 4.00556L5.73828 7.41237L3.84654 5.52063C3.74051 5.4146 3.61495 5.36159 3.46986 5.36159C3.32477 5.36159 3.19922 5.4146 3.09319 5.52063L2.33147 6.27398C2.23102 6.37442 2.1808 6.50277 2.1808 6.65902C2.1808 6.80969 2.23102 6.93525 2.33147 7.0357L5.36161 10.0658C5.46763 10.1719 5.59319 10.2249 5.73828 10.2249C5.88895 10.2249 6.0173 10.1719 6.12332 10.0658L10.6685 5.52063C10.769 5.42018 10.8192 5.29462 10.8192 5.14396ZM12.9286 6.49998C12.9286 7.66628 12.6412 8.74189 12.0664 9.72682C11.4916 10.7118 10.7118 11.4916 9.72684 12.0664C8.74191 12.6412 7.66629 12.9286 6.5 12.9286C5.3337 12.9286 4.25809 12.6412 3.27316 12.0664C2.28822 11.4916 1.50837 10.7118 0.933592 9.72682C0.358815 8.74189 0.0714264 7.66628 0.0714264 6.49998C0.0714264 5.33369 0.358815 4.25807 0.933592 3.27314C1.50837 2.28821 2.28822 1.50835 3.27316 0.933576C4.25809 0.3588 5.3337 0.0714111 6.5 0.0714111C7.66629 0.0714111 8.74191 0.3588 9.72684 0.933576C10.7118 1.50835 11.4916 2.28821 12.0664 3.27314C12.6412 4.25807 12.9286 5.33369 12.9286 6.49998Z"
								fill="currentColor"
							/>
						</svg>
						{{ item[0] }} ({{ item[1].length }})
					</div>
				</div>
				<div
					style="padding: 30px; text-align: center; padding-bottom: 10px; font-style: italic"
					v-else
				>
					Слов в словаре: {{ allWords.length }}
				</div>
			</div>
			<div class="modal-content-buttons">
				<Button
					design="action"
					style="width: 200px"
					:disabled="studiedWords.length < WORDS"
					@click="
						wordsModalOpen = false;
						searchWord = '';
						trainingList = genSentences(studiedWords);
						trainingIndex = 0;
						textFieldFocus();
					"
					>ОК</Button
				><br />
				<Button design="link" @click="clearWords" style="margin-top: 5px"
					>Очистить список</Button
				>
			</div>
		</Modal>

		<Modal
			:open="successModalOpen"
			:width="600"
			key="success-modal"
		>
			<div class="modal-content" style="text-align: center; font-weight: 700; font-size: 25px; color: #5FAA44">
				Cool!
			</div>
			<div class="modal-content-buttons">
				<Button
					design="action"
					style="width: 200px"
					@click="again(true)"
				>Другие слова</Button><br />
				<Button
					design="link"
					@click="again(false)"
					style="margin-top: 5px"
				>Продолжить эти</Button>
			</div>
		</Modal>
	</Page>
</template>

<script>
// важно, съедут айдишники при обновлении словаря
import Page from '@/components/Page';
import Button from '@/components/Button';
import ButtonProgress from '@/components/ButtonProgress';
import TextField from '@/components/TextField';
import Modal from '@/components/Modal';

const WORDS = 7;
const REPEATS = 1;//4;

export default {
	name: 'MvpMain',
	components: {
		Page,
		Button,
		ButtonProgress,
		TextField,
		Modal,
	},
	props: ['words', 'sent'],
	data: function() {
		const studiedWords = []; /* JSON.parse(
			localStorage.getItem('studiedWords') || '[]'
		); */
		return {
			allWords: this.words.map(([word, sent], i) => [word, sent, i]),
			allSentences: this.sent,
			WORDS,

			loading: false,
			studiedWords,

			wordsModalOpen: true,
			searchWord: '',

			trainingList:
				studiedWords.length === 0 ? null : this.genSentences(studiedWords),
			trainingIndex: 0,
			answer: '',

			successModalOpen: false
		};
	},
	computed: {
		wordsFound() {
			return this.searchWord === ''
				? this.studiedWords.map(id => this.allWords[id])
				: this.allWords.filter(([word]) => word.includes(this.searchWord));
		},

		cardAnswerPre() {
			const sentence = this.trainingList[this.trainingIndex].sentence[0];
			const word = this.trainingList[this.trainingIndex].word[0];
			const wordIndex = sentence.toLowerCase().indexOf(word.toLowerCase());
			return sentence.slice(0, wordIndex);
		},

		cardAnswerPost() {
			const sentence = this.trainingList[this.trainingIndex].sentence[0];
			const word = this.trainingList[this.trainingIndex].word[0];
			const wordIndex = sentence.toLowerCase().indexOf(word.toLowerCase());
			return sentence.slice(wordIndex + word.length);
		},

		wordLength: function() {
			const div = document.createElement('div');
			div.textContent = this.trainingList[this.trainingIndex].word[0];

			// todo: font & font size
			div.style.fontSize = '20px';
			div.style.position = 'absolute';
			div.style.top = '-10000px';
			div.style.left = '-10000px';
			document.body.appendChild(div);
			const s = getComputedStyle(div).width;
			document.body.removeChild(div);
			return parseInt(s) + 35;
		},
	},
	methods: {
		textFieldFocus() {
			setTimeout(() => this.$refs.textField?.focus(), 10);
		},

		onWordClick(word) {
			if (this.studiedWords.includes(word[2])) {
				this.removeWord(word);
			} else {
				this.addWord(word);
			}
		},

		removeWord(word) {
			const index = this.studiedWords.indexOf(word[2]);
			if (index > -1) {
				this.studiedWords.splice(index, 1);
			}
			this.updateWords();
		},

		addWord(word) {
			this.studiedWords.push(word[2]);
			this.updateWords();
		},

		clearWords() {
			this.studiedWords = [];
			this.updateWords();
		},

		updateWords() {
		//	console.log('upd');
		//	localStorage.setItem('studiedWords', JSON.stringify(this.studiedWords));
		},

		genSentences(studiedWords) {
			const indices = generateRandomRange(studiedWords.length - 1, Math.min(WORDS, studiedWords.length));
			while (indices.length < Math.min(WORDS, studiedWords.length) * REPEATS) {
				indices.push(
					...indices.slice().sort(() => (Math.random() > 0.5 ? -1 : 1))
				);
			}
			return indices.map(index => {
				const wordId = studiedWords[index];
				const word = this.allWords[wordId];
				const sentences = word[1];
				const sentenceId = sentences[rand(0, sentences.length - 1)];
				return {
					word,
					sentence: this.allSentences[sentenceId]
				};
			});
		},

		checkNext() {
			const word = this.trainingList[this.trainingIndex].word[0];
			const answer = this.answer;

			if (word.toLowerCase() !== answer.toLowerCase()) {
				return;
			}

			if (this.trainingIndex === this.trainingList.length - 1) {
				this.successModalOpen = true;
		//		this.trainingIndex = 0;
		//		this.trainingList = this.genSentences(this.studiedWords);
			} else {
				this.trainingIndex++;
				this.textFieldFocus();
			}
			this.answer = '';
		},

		again(updateWords) {
			this.successModalOpen = false;
			if (updateWords) {
				this.wordsModalOpen = true;
			} else {
				this.trainingList = this.genSentences(this.studiedWords);
				this.trainingIndex = 0;
				this.textFieldFocus();
			}
		}
	},
};

function rand(min, max) {
	return min + Math.round(Math.random() * (max - min));
}

function generateRandomRange(max, length) {
	const range = [];
	while (range.length < length) {
		const id = rand(0, max);
		if (!range.includes(id)) {
			range.push(id);
		}
	}
	return range;
}
</script>

<style lang="stylus">
@import "../components/style.styl";

.studied-words
	text-align center
	padding 50px 0
	& > div
		display inline-block

.training-page
	text-align center

.card
	display inline-block

.buttons-panel
	text-align left
	a
		font-size 14px
		margin-right 15px
		color #aaa
	a:hover
		text-decoration none
		color #777x

.translate-text
	margin-top 120px
	font-family: main-font
	font-weight 300
	font-size 20px
	margin-bottom 50px

.original-text
	font-family: main-font
	font-size 20px
	margin-bottom 60px
	.original-text-input
		display inline-block
		& >>> input
			text-align center
			font-size 20px !important
			font-weight 500
			color #999
			&.right
				color main-color
			&.wrong
				color red

.button-container .button
	width 160px

.modal-content
	padding 30px
	.top p
		margin-bottom 10px

.modal-content-buttons
	padding-bottom 20px
	padding-top 0
	text-align center

.add-panel-items
	padding 10px 0
	max-height 50vh
	overflow auto
	.item
		font-weight 400
		cursor pointer
		line-height 32px
		text-align left
		color #444
		padding-left 3px
		svg
			color #D7D7D7
			transform scale(1.2)
			margin-right 10px
			vertical-align middle
	.item.selected
		font-weight 600
		svg
			color main-color
	.item.create-item
		font-style italic
		font-weight 300
		svg
			color #B6B6B6

.placeholder
	text-align center
</style>
