<template>
	<div
		:class="[
			'button',
			'design_' + design,
			'size_' + size,
			disabled && 'disabled',
		]"
		@click="$emit('click', $event)"
	>
		<div v-if="showSpinner" class="spinner-container">
			<Spinner size="small" design="action" />
		</div>
		<slot v-if="showContent" />
	</div>
</template>

<script>
import Spinner from '@/components/Spinner';

export default {
	name: 'Button',
	props: {
		size: {
			validator: val => ['medium', 'big'].includes(val),
			default: 'medium',
		},
		design: {
			validator: val =>
				['default', 'action', 'ghost', 'link', 'danger'].includes(val),
			default: 'default',
		},
		showSpinner: {
			type: Boolean,
			default: false,
		},
		showContent: {
			type: Boolean,
			default: true,
		},
		disabled: {
			type: Boolean,
			default: false,
		},
	},
	components: {
		Spinner,
	},
};
</script>

<style lang="stylus" scoped>
@import "./style.styl";

.button
	display inline-block
	box-sizing border-box
	cursor pointer
	padding 0 15px
	border-radius 4.8px
	color white
	font-family heading-font
	text-align center
	&.design_action
		background main-color
		&:active
			background main-color-darker
	&.design_ghost
		box-shadow inset 0 0 0 3px #ddd
		color #888
		&:hover
			color #777
		&:active
			background rgba(0, 0, 0, 0.05)
	&.design_danger
		background main-red
		&:active
			background main-red-darker
	&.design_link
		color #7292f6
	&.size_medium
		font-size 14px
		line-height 35px
		height 35px
	&.size_big
		font-size 16px
		line-height 43px
		height 43px
		.spinner-container
			padding-top 6px
	&.disabled
		background #777
		cursor not-allowed

.spinner-container
	display inline-block
	vertical-align top
</style>
