<template>
	<div :class="['text-field-container', 'partof_' + partof, 'size_' + size]">
		<input
			class="text-field"
			:value="value"
			@input="$emit('input', $event.target.value)"
			@blur="$emit('blur', $event)"
			@keypress="$emit('keypress', $event)"
			:placeholder="placeholder"
			:type="type"
			:disabled="disabled"
			:autofocus="autofocus"
			:autocomplete="autocomplete"
			ref="input"
		/>
		<div v-if="hint" :class="['text-field-hint', 'design_' + hintDesign]">
			{{ hint }}
		</div>
	</div>
</template>

<script>
export default {
	name: 'TextField',
	props: {
		value: String,
		placeholder: String,
		type: {
			type: String,
			default: 'text',
		},
		disabled: {
			type: Boolean,
			default: false,
		},
		autofocus: {
			type: Boolean,
			default: false,
		},
		autocomplete: String,

		size: {
			validator: val => ['medium', 'big'].includes(val),
			default: 'medium',
		},
		partof: {
			validator: val => ['top', 'middle', 'bottom', 'nothing'].includes(val),
			default: 'nothing',
		},
		hint: String,
		hintDesign: {
			validator: val => ['default', 'action'].includes(val),
			default: 'default',
		},
	},

	methods: {
		focus: function() {
			this.$refs.input.focus();
		},

		blur: function() {
			this.$refs.input.blur();
		},
	},
};
</script>

<style lang="stylus">
@import "./style.styl";

.text-field-container
	width 100%
	display grid
	grid-template-columns 100% 200px
	&.partof_top
		.text-field
			border-bottom-left-radius 0
			border-bottom-right-radius 0
	&.partof_bottom
		.text-field
			border-top-left-radius 0
			border-top-right-radius 0
			border-top 0
	&.size_medium
		.text-field
			font-size 16px
			line-height 33px
		.text-field-hint
			line-height 35px
	&.size_big
		.text-field
			font-size 20px
			line-height 41px
		.text-field-hint
			line-height 43px

.text-field
	width 100%
	box-sizing border-box
	font-family main-font
	padding 0 15px
	background white
	border solid 1px #D2D2D2
	outline 0
	border-radius 4.8px
	&::placeholder
		font-weight 300

.text-field-hint
	font-family main-font
	word-break keep-all
	padding-left 10px
	&.design_action
		color main-color-darker
</style>
