import {
	getCourse,
	createCourse,
	searchWord,
	getRandomSentences,
} from '@/lib/api';

export default {
	namespaced: true,

	state: () => ({
		loading: false,
		title: '',
		name: '',

		wordQ: '',
		wordListLoading: false,
		wordList: null,
		wordListChosen: [],

		chosenWords: [],
	}),

	actions: {
		create({ commit }) {
			commit('create');
		},

		async edit({ commit }, courseName) {
			commit('startLoading');
			const data = await getCourse(courseName);
			commit('edit', data);
			commit('finishLoading');
		},

		async save({ commit, state }, saveType) {
			commit('startLoading');
			if (saveType === 'create') {
				await createCourse({
					coursename: state.name,
					title: state.title,
					wordsIds: state.chosenWords.map(word => word.id),
				});
			}
			commit('finishLoading');
		},

		async loadWordList({ commit, state }) {
			commit('wordListStartLoading');
			const data = await searchWord(state.wordQ);
			commit('wordListFinishLoading', data);
		},

		async addSelectedWords({ commit, state }) {
			const { wordListChosen, wordList, chosenWords } = state;

			const words = wordListChosen
				.map(id => {
					if (chosenWords.find(word => word.id === id)) {
						return null;
					}
					const word = wordList?.find(word => word.id === id);
					word.sentence = null;
					return word;
				})
				.filter(x => x);

			if (words.length === 0) {
				return;
			}

			commit('pushChosenWords', words);

			const sentences = await getRandomSentences(words.map(word => word.id));
			sentences.forEach((data, i) => {
				commit('setChosenWordSentence', {
					wordId: words[i].id,
					sentence: data.sentence,
				});
			});
		},

		async updateSentence({ commit }, wordId) {
			commit('setChosenWordSentence', {
				wordId,
				sentence: null,
			});
			const data = await getRandomSentences([wordId]);
			commit('setChosenWordSentence', {
				wordId,
				sentence: data[0].sentence,
			});
		},
	},

	mutations: {
		create(state) {
			state.title = '';
			state.name =
				'my-course-' + Math.floor(Math.random() * 99999999).toString(16);
			state.loading = false;
		},

		edit(state, data) {
			state.title = data.title;
			state.name = data.coursename;
			state.loading = false;
		},

		startLoading(state) {
			state.loading = true;
		},

		finishLoading(state) {
			state.loading = false;
		},

		setTitle(state, value) {
			state.title = value;
		},

		setName(state, value) {
			state.name = value;
		},

		setWordQ(state, value) {
			state.wordQ = value;
			state.wordListChosen = [];
		},

		wordListStartLoading(state) {
			state.wordListLoading = true;
			state.wordList = null;
		},

		wordListFinishLoading(state, data) {
			state.wordListLoading = false;
			state.wordList = data;
		},

		selectWord(state, id) {
			const index = state.wordListChosen.indexOf(id);
			if (index === -1) {
				state.wordListChosen.push(id);
			} else {
				state.wordListChosen.splice(index, 1);
			}
		},

		pushChosenWords(state, words) {
			state.chosenWords.push(...words);
		},

		setChosenWordSentence(state, { wordId, sentence }) {
			const word = state.chosenWords.find(word => word.id === wordId);
			const index = state.chosenWords.indexOf(word);
			if (word) {
				state.chosenWords.splice(index, 1);
				state.chosenWords.splice(index, 0, {
					...word,
					sentence: sentence,
				});
			}
		},
	},
};
