<template>
	<Page :loading="loading">
		<div class="list">
			<CourseBlock
				v-for="course in data"
				:key="course.id"
				:title="course.title"
				:avatar="course.avatar"
				@click="$router.push(`/course/${course.coursename}`)"
			/>
		</div>
	</Page>
</template>

<script>
import CourseBlock from '@/components/CourseBlock';
import Page from '@/components/Page';

export default {
	name: 'CourseListAllPage',

	computed: {
		loading: function() {
			return this.$store.state.courseListAll.loading;
		},

		data: function() {
			return this.$store.state.courseListAll.data;
		},
	},

	mounted: function() {
		this.$store.dispatch('courseListAll/load');
	},

	components: {
		CourseBlock,

		Page,
	},
};
</script>

<style lang="stylus">
.list
	display grid
	grid-template-columns 150px 150px 150px
	grid-row-gap 20px
	grid-column-gap 15px
	justify-content center
</style>
