import { getCourseById } from '@/lib/api';

export default {
	namespaced: true,

	state: () => ({
		loading: true,
		data: null,
	}),

	actions: {
		load: async function({ commit }, { courseId, authorId }) {
			commit('startLoading');
			const data = await getCourseById(courseId, authorId);
			commit('accomplishLoading', data);
		},
	},

	mutations: {
		startLoading(state) {
			state.loading = true;
			state.data = null;
		},

		accomplishLoading(state, data) {
			state.loading = false;
			state.data = data;
		},
	},
};
