<template>
	<Page :loading="loading">
		<div class="training-page" v-if="!loading">
			<div class="card">
				<div class="buttons-panel">
					<a href="#" @click="help">Подсказка</a>
					<a href="#">{{
						card.memesCount === 0 ? 'Добавить мем' : `Мемы (${card.memesCount})`
					}}</a>
				</div>
				<div class="translate-text">
					{{ cardQuestion }}
				</div>
				<div class="original-text">
					{{ cardAnswerPre }}
					<TextField
						:value="$store.state.courseTraining.answerToSend"
						@input="$store.commit('courseTraining/setAnswer', $event)"
						@keypress="$event.key === 'Enter' && next()"
						class="original-text-input"
						:style="{ width: wordLength + 'px' }"
						ref="textField"
					/>
					{{ cardAnswerPost }}
				</div>
			</div>
			<div class="button-container">
				<ButtonProgress
					:progress="progress"
					design="action"
					size="big"
					@click="next"
				>
					Next
				</ButtonProgress>
			</div>
		</div>
	</Page>
</template>

<script>
import ButtonProgress from '@/components/ButtonProgress';
import Page from '@/components/Page';
import TextField from '@/components/TextField';

export default {
	name: 'CourseTrainingPage',

	computed: {
		loading: function() {
			return this.$store.state.courseTraining.loading;
		},

		card: function() {
			const { data, currentCardIndex } = this.$store.state.courseTraining;
			return data[currentCardIndex];
		},

		cardQuestion: function() {
			const { card } = this;
			return card.question.join('');
		},

		cardAnswerPre: function() {
			const { card } = this;
			return card.answer[0];
		},

		cardAnswerPost: function() {
			const { card } = this;
			return card.answer[2];
		},

		cardAnswer: function() {
			const { card } = this;
			return card.answer[1];
		},

		wordLength: function() {
			const div = document.createElement('div');
			div.textContent = this.cardAnswer;

			// todo: font & font size
			div.style.fontSize = '20px';
			div.style.position = 'absolute';
			div.style.top = '-10000px';
			div.style.left = '-10000px';
			document.body.appendChild(div);
			const s = getComputedStyle(div).width;
			document.body.removeChild(div);
			return parseInt(s) + 35;
		},

		progress: function() {
			const { data, currentCardIndex } = this.$store.state.courseTraining;
			return currentCardIndex / data.length;
		},
	},

	mounted: async function() {
		const courseName = this.$route.params.courseName;
		const user = this.$store.state.user;
		await this.$store.dispatch('courseTraining/start', {
			courseId: user + '/' + courseName,
		});
		this.textFieldFocus();
	},

	methods: {
		textFieldFocus: function() {
			this.$refs.textField?.focus();
		},

		next: function() {
			this.$store.commit('courseTraining/nextCard');
			this.textFieldFocus();
		},

		help: function() {
			this.$store.commit('courseTraining/help');
			this.textFieldFocus();
		},
	},

	components: {
		ButtonProgress,
		Page,
		TextField,
	},
};
</script>

<style lang="stylus" scoped>
@import "../components/style.styl";

.training-page
	text-align center

.card
	display inline-block

.buttons-panel
	text-align left
	a
		font-size 14px
		margin-right 15px
		color #aaa
	a:hover
		text-decoration none
		color #777x

.translate-text
	margin-top 120px
	font-family: main-font
	font-weight 300
	font-size 20px
	margin-bottom 50px

.original-text
	font-family: main-font
	font-size 20px
	margin-bottom 60px
	.original-text-input
		display inline-block
		& >>> input
			text-align center
			font-size 20px !important
			font-weight 500
			color #999
			&.right
				color main-color
			&.wrong
				color red

.button-container .button
	width 160px
</style>
