import { courseGet10Training } from '@/lib/api';

export default {
	namespaced: true,

	state: () => ({
		loading: true,
		data: null,
		currentCardIndex: null,
		answerToSend: null,
	}),

	actions: {
		start: async function({ commit }, { courseId }) {
			commit('startLoading');
			const data = await courseGet10Training(courseId);
			commit('accomplishLoading', data);
		},
	},

	mutations: {
		startLoading(state) {
			state.loading = true;
			state.data = null;
			state.currentCardIndex = null;
			state.answerToSend = null;
		},

		accomplishLoading(state, data) {
			state.loading = false;
			state.data = data;
			state.currentCardIndex = 0;
			state.answerToSend = '';
		},

		nextCard(state) {
			state.currentCardIndex++;
			state.answerToSend = '';

			// if state.currentCardIndex > state.data.length
		},

		setAnswer(state, value) {
			state.answerToSend = value;
		},

		help(state) {
			let current = state.answerToSend;
			const answer = state.data[state.currentCardIndex].answer[1];

			if (current === answer) {
				return;
			}

			let i = 0;

			if (current.length > answer.length) {
				current = current.slice(0, answer.length);
			}

			for (; i < answer.length; i++) {
				if (current[i] !== answer[i]) {
					break;
				}
			}

			if (current[i]) {
				current = current.slice(0, i);
			} else {
				current = current.slice(0, i) + answer[i];
			}

			state.answerToSend = current;
		},
	},
};
