export const debounceAfter = function(fn, delay) {
	let timeoutId = null;
	return function() {
		return new Promise((resolve, reject) => {
			if (timeoutId !== null) {
				clearTimeout(timeoutId);
			}
			timeoutId = setTimeout(() => {
				timeoutId = null;
				fn.apply(this, arguments).then(resolve, reject);
			}, delay);
		});
	};
};
