<template>
	<Paranja :open="open" class="modal-paranja">
		<div
			:class="['modal', loading && 'loading']"
			:style="{
				width: width + 'px',
			}"
		>
			<Spinner v-if="loading" />
			<slot v-else />
		</div>
	</Paranja>
	<!-- <portal to="portal-main">
		<p>This slot content will be rendered wherever the with name 'destination' is  located.</p>
	</portal> -->
</template>

<script>
import Paranja from '@/components/Paranja';
import Spinner from '@/components/Spinner';

export default {
	name: 'Modal',
	props: {
		open: {
			type: Boolean,
			required: true,
		},
		loading: {
			type: Boolean,
			default: false,
		},
		width: {
			type: Number,
		},
	},
	components: {
		Paranja,
		Spinner,
	},
};
</script>

<style lang="stylus" scoped>
@import "./style.styl";

.paranja
	display grid
	align-items center

.modal
	background white
	border-radius 7px
	display inline-block
	position absolute
	top 50%
	left 50%
	transform translate(-50%, -50%)
	&.loading
		padding 50px 0
		text-align center
</style>
